<script>
export default {
  data() {
    return {
      count: 2,
      count2: 2,
      fishInterval: 1,
      fishTimeout: 1,
      x: 0,
    };
  },
  components: {
    // Header
  },
  mounted() {
    // this.$emit("showheader", true);
    this.water();
    this.fishTimeout = setTimeout(() => {
      this.fishInterval = setInterval(this.fish, 200);
    }, 1500);
  },
  methods: {
    //欄位點擊跑出子連結
    produce(name) {
      if (name === "keep") {
        if (this.count % 2 != 0) {
          this.count = 2;
        } else {
          this.count = 1;
        }
      }

      if (name == "clean") {
        if (this.count2 % 2 != 0) {
          this.count2 = 2;
        } else {
          this.count2 = 1;
        }
      }
    },
    //水填滿canvas
    water() {
      var unit = 100;
      var canvas;
      var context;
      var height;
      var width;
      var xAxis;
      var yAxis;
      var percentMax = 100; //液體占容器的百分比
      var percentMin = 0;
      var maxHeight = 1;
      if (percentMax >= 1) {
        maxHeight = 0.95;
      }
      var wavePower = 3;

      //初始化
      function init() {
        window.requestAnimFrame = (() => {
          return (
            window.requestAnimationFrame ||
            window.webkitRequestAnimationFrame ||
            window.mozRequestAnimationFrame ||
            function (callback) {
              window.setTimeout(callback, 10 / 10);
            }
          );
        })();
        canvas = document.getElementById("sineCanvas");
        var wid = document.getElementById("home");
        canvas.width = wid.clientWidth;
        canvas.height = wid.clientHeight;
        console.log(wid.clientWidth);
        console.log(wid.clientHeight);
        context = canvas.getContext("2d");
        height = canvas.height;
        width = canvas.width;
        yAxis = 0;

        draw();
      }
      function draw() {
        // 清理canvas畫板
        context.clearRect(0, 0, width, height);
        //描繪波浪
        drawWave(["#77DDFF", "#0044BB"], 1, 1.5);

        if (percentMin <= percentMax) {
          percentMin = percentMin + 0.009;
          xAxis = Math.floor(height * (maxHeight - percentMin));
        }
        if (percentMin > 0.8) {
          if (wavePower <= 36 * percentMax) {
            wavePower = wavePower + 0.05;
          }
        }
        // 更新時間然後重新繪制
        draw.seconds = draw.seconds + 0.009;
        draw.t = draw.seconds * Math.PI;
        window.requestAnimFrame(draw);
      }
      draw.seconds = 0;
      draw.t = 0;
      /**
       * 描繪波浪
       * drawWave([漸變左側颜色，右側颜色], 不透明度, 波浪的幅度)
       */
      function drawWave(color, alpha, zoom) {
        context.globalAlpha = alpha;
        var linearGrad = context.createLinearGradient(0, 0, width, 0);
        linearGrad.addColorStop(0.0, color[0]);
        linearGrad.addColorStop(1.0, color[1]);
        context.fillStyle = linearGrad;

        context.beginPath(); //開始路徑
        drawSine(draw.t / 0.3, zoom);
        context.lineTo(width + 10, height); //路徑朝canvas右下
        context.lineTo(0, height); //路徑朝canvas左下
        context.closePath(); //封閉路徑
        context.fill(); //塗色
      }
      /**
       * 畫正弦
       * drawSine(時間, 波浪的浮動)
       */
      function drawSine(t, zoom) {
        var x = t; //將時間轉換為x軸
        var y = Math.sin(x) / zoom;
        context.moveTo(yAxis, unit * y + xAxis); //在開始位置放置路径

        // 繪制波浪、横向幅度
        for (var i = yAxis; i <= width + 10; i += 10) {
          x = t + (-yAxis + i) / unit / zoom;
          y = Math.sin(x - 0) / wavePower;
          context.lineTo(i, unit * y + xAxis);
        }
      }
      init();
    },
    //魚動畫
    fish() {
      var pic = document.getElementById("fish1");
      var pic2 = document.getElementById("fish2");
      var pic3 = document.getElementById("fish3");
      pic.style.display = "block";
      pic.classList.add("animated");
      pic.classList.add("fadeIn");
      setTimeout(() => {
        pic2.style.display = "block";
        pic2.classList.add("animated");
        pic2.classList.add("fadeIn");
      }, 300);

      setTimeout(() => {
        pic3.style.display = "block";
        pic3.classList.add("animated");
        pic3.classList.add("fadeIn");
      }, 500);

      setTimeout(() => {
        var wid = document.getElementById("home");
        pic.style.right = this.x + 100 + "px";
        pic2.style.right = this.x + 45 + "px";
        pic3.style.right = this.x + "px";
        this.x = this.x + 10;
        if (wid !== null) {
          if (this.x >= wid.clientWidth) {
            this.x = 0;
          } else {
            this.x = this.x + 10;
          }
        }
      }, 1000);
    },
    //轉換路由位置
    changePage(page, index) {
      // 將目前被點擊到的項目index儲存到vuex
      // 因為每次點擊home裡的選項都會到app重新渲染router-view，因此在app裡讀取vuex，並將讀取到的index傳到herder組件
      this.$store.commit("changeIndex", index);
      this.$router.push(page);
    },
  },
  destroyed() {
    clearInterval(this.fishInterval);
    clearTimeout(this.fishTimeout);
  },
};
</script>
